.dot {
  color: #fff;
  height: 8px;
  text-align: center;
  width: 8px;
  background: #000;
  border-radius: 100%;
  line-height: 8px;
}

.dotOne {
  background: #ff5353;
  animation: dotOneKeyframes 5s infinite;
  position: absolute;
  top: 0;
  left: 0;
}

.dotTwo {
  background: #971919;
  animation: dotTwoKeyframes 5s .416667s infinite;
  position: absolute;
  top: 0;
  left: 14px;
}

.dotThree {
  background: #530404;
  animation: dotThreeKeyframes 5s .833333s infinite;
  position: absolute;
  top: 14px;
  left: 14px;
}

@keyframes dotOneKeyframes {
  0% {
    top: 0;
    left: 0;
  }

  8.33333% {
    top: 14px;
    left: 0;
  }

  25% {
    top: 14px;
    left: 0;
  }

  33.3333% {
    top: 14px;
    left: 14px;
  }

  50% {
    top: 14px;
    left: 14px;
  }

  58.3333% {
    top: 0;
    left: 14px;
  }

  75% {
    top: 0;
    left: 14px;
  }

  83.3333% {
    top: 0;
    left: 0;
  }
}

@keyframes dotTwoKeyframes {
  0% {
    top: 0;
    left: 14px;
  }

  8.33333% {
    top: 0;
    left: 0;
  }

  25% {
    top: 0;
    left: 0;
  }

  33.3333% {
    top: 14px;
    left: 0;
  }

  50% {
    top: 14px;
    left: 0;
  }

  58.3333% {
    top: 14px;
    left: 14px;
  }

  75% {
    top: 14px;
    left: 14px;
  }

  83.3333% {
    top: 0;
    left: 14px;
  }
}

@keyframes dotThreeKeyframes {
  0% {
    top: 14px;
    left: 14px;
  }

  8.33333% {
    top: 0;
    left: 14px;
  }

  25% {
    top: 0;
    left: 14px;
  }

  33.3333% {
